import React, { Component } from 'react';
import { TimelineLite, Power2 } from "gsap";

export default class CursorOverlay extends Component {
  cursorOverlay = React.createRef();
  cursorOverlayCursor = React.createRef();

  componentDidMount(){
    this.cursorDimensions = this.cursorOverlay.current.getBoundingClientRect();

    this.props.items.forEach(hoverItem => {
      hoverItem.addEventListener("mouseenter", this.onMouseEnter.bind(this));
      hoverItem.addEventListener("mouseleave", this.onMouseLeave.bind(this));
      hoverItem.addEventListener("mousemove", this.onMouseMove.bind(this));
      hoverItem.addEventListener("click", this.onClick.bind(this));
      hoverItem.addEventListener("dragstart", () => { return false });
    })
  }

  onMouseEnter(){
    this.cursorOverlay.current.classList.remove("-hidden");
    this.cursorOverlayCursor.current.style.transform = "scale(0.4)";
  }

  onMouseLeave(){
    this.cursorOverlay.current.classList.add("-hidden");
    this.cursorOverlayCursor.current.style.transform = "scale(0)";
  }

  onMouseMove(e){
    let x = e.clientX - (this.cursorDimensions.width / 2),
        y = e.clientY - (this.cursorDimensions.height / 2);
    
    this.cursorOverlay.current.style.transform = `translate(${x}px, ${y}px)`;
  }

  onClick(){
    const rippleTime = 0.4,
          easing = Power2.easeOut;

    let tl = new TimelineLite();

    tl.set(this.cursorOverlayCursor.current, {scale: 0.1, opacity: 0, transition: "none"})
      .set(this.cursorOverlay.current, {transition: "none"})
      .to(this.cursorOverlayCursor.current, rippleTime * 0.3, {scale: 1, opacity: 0.5, ease: easing})
      .to(this.cursorOverlayCursor.current, rippleTime * 0.2, {scale: 1, opacity: 0.7, ease: easing})
      .to(this.cursorOverlayCursor.current, rippleTime * 0.2, {scale: 0.1, opacity: 0, ease: easing})
      .add(() => { 
        this.cursorOverlayCursor.current.removeAttribute('style');
        this.cursorOverlay.current.removeAttribute('style');
        this.cursorOverlay.current.classList.add("-hidden");
      }) // reset after timline styles
  }

  render() {
    return (
      <div ref={ this.cursorOverlay } className="cursorOverlay -hidden">
        <div ref={ this.cursorOverlayCursor } className="cursorOverlay-cursor" style={{ transform: `scale(0) translate(-50%, -50%)` }}/>
      </div>
    );
  }
}


